const TYPES_ERRORS = {
  400: {
    title: "Bad request",
    message: "   ",
  },
  403: {
    title: " ",
    message: "    ",
  },
  404: {
    title: "  ",
    message: ",     ",
  },
  500: {
    title: "    ",
    message: "  ",
  },
};
export default {
  props: {
    errorCode: Number,
  },
  data: () => ({
    error: {
      code: null,
      title: null,
      message: null,
    },
  }),
  mounted() {
    this.error = {
      code: this.errorCode,
      ...TYPES_ERRORS[this.errorCode],
    };
  },
};
